import React, {
  useCallback, useMemo, useRef
} from 'react';
import {
  Box, Flex, HStack, Text, useClipboard, VStack
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';

import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import { useAppSelector } from '../../../hooks/store';
import { currencyFormat } from '../../../utils/general';
import Icon from '../../../components/Icon';
import ContactList from '../../contacts/ContactList/ContactList';
import TransactionsList from '../../transactions/TransactionList/TransactionsList';
import CardList, { CardListRef } from '../../cards/CardList/CardList';
import style from './Dashboard.module.css';
import { useDrawer } from '../../../hooks/drawer';

const AccountDetails = React.lazy(() => import('../../ao/Account/AccountDetails/AccountDetails'));
const BusinessDetails = React.lazy(() => import('../../ao/Account/BusinessDetails/BusinessDetails'));

/**
 * Dashboard Page
 * @returns
 */
const Dashboard = () => {
  const { t } = useTranslation();
  const { open: openDrawer } = useDrawer();
  const navigate = useNavigate();

  const user = useAppSelector((store) => store.user.personDetails, shallowEqual);
  const { selectedAccount: account, selectedAccountType: accountType } = useAppSelector((store) => store.account, shallowEqual);
  const business = useAppSelector((store) => store.business.selectedBusiness, shallowEqual);

  const { hasCopied: hasAccountCopied, onCopy: onAccountCopy } = useClipboard(account?.accountNumber || '', 2000);

  const cardRef = useRef<CardListRef>(null);

  const headerText = useMemo(() => {
    if (business) {
      return business.legalName;
    }
    return `${user?.firstName} ${user?.lastName}`;
  }, [user, business]);

  const accountDetails = useCallback(() => {
    openDrawer(AccountDetails);
  }, []);

  const bussinessDetails = useCallback(() => {
    openDrawer(BusinessDetails, {
      id: business?.id
    });
  }, [business]);

  const fund = useCallback(() => {
    navigate('/fund');
  }, []);

  const sendMoney = useCallback(() => {
    navigate('/contacts');
  }, []);

  return (

    <div className="page" id="dashboard-page">

      <Box mb="24px">
        <Flex>
          <Text data-testid="heading" variant="large" mr="auto">{headerText}</Text>
          {business && business.id !== 'personChecking' ? (
            <Text variant="small" cursor="pointer" className="text-light" onClick={bussinessDetails}>
              View Details
            </Text>
          ) : null}
        </Flex>
      </Box>

      <Box mb="24px">
        <Flex gap="24px" flexWrap={{ base: 'wrap', md: 'nowrap' }}>

          <Box
            width={{ base: '100%', md: account?.type !== 'cardAccount' ? '50%' : '100%' }}
            borderRadius="4px"
            backgroundColor="var(--dashboard-card-background-color)"
            color="var(--dashboard-card-text-color)"
            padding="24px"
          >
            <Flex mb="12px" justifyContent="space-between">
              <Text className={style.accountLabel} lineHeight="24px" fontSize="20px">{account?.label}</Text>
              <Text cursor="pointer" fontSize="14px" textDecoration="underline" onClick={accountDetails} className={style.hideItInMobile}>View Details</Text>

              <Text display="none" fontWeight="bold" textAlign="left" mb="8px" fontSize="14px" className={style.showItInMobile}>{account ? currencyFormat(account.availableBalance, account.currency) : null }</Text>

            </Flex>
            <Text fontWeight="bold" textAlign="left" mb="34px" fontSize="30px" className={style.hideItInMobile}>{account ? currencyFormat(account.availableBalance, account.currency) : null }</Text>
            <Flex justify="space-between">
              <Flex gap="21px" alignItems="center">
                <Text color="var(--dashboard-card-text-color)" variant="regular">
                  {t('dashboard.dashboard_accNo_title')}
                  {' '}
                  {account?.accountNumber}
                </Text>
                {hasAccountCopied
                  ? <Icon icon="tick" cursor="pointer" size={12} />
                  : <Icon onClick={onAccountCopy} icon="copy" cursor="pointer" size={12} /> }
              </Flex>
              <Text display="none" cursor="pointer" fontSize="14px" textDecoration="underline" color="var(--dashboard-card-text-color)" onClick={accountDetails} className={style.showItInMobile}>View Details</Text>
            </Flex>
          </Box>

          {account?.type !== 'cardAccount'
            ? (
              <VStack width={{ base: '100%', md: '50%' }}>

                <Box cursor="pointer" width="100%" padding="16px 24px 16px 16px" backgroundColor="var(--list-item-background)" onClick={fund}>
                  <HStack justifyContent="space-between">
                    <div className="details">
                      <Text fontSize="20px" lineHeight="24px" paddingBottom="12px" variant="regular" textAlign="left">
                        {t('dashboard.dashboard_row_fund_title')}
                      </Text>
                      <Text variant="small" textAlign="left">
                        {t('dashboard.dashboard_row_fund_desc', {
                          value: account?.label
                        })}
                      </Text>
                    </div>
                    <Icon style={{ marginLeft: '8px' }} icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
                  </HStack>
                </Box>

                <Box cursor="pointer" width="100%" padding="16px 24px 16px 16px" backgroundColor="var(--list-item-background)" onClick={sendMoney}>
                  <HStack justifyContent="space-between">
                    <div className="details">
                      <Text fontSize="20px" lineHeight="24px" paddingBottom="12px" variant="regular" textAlign="left">
                        {t('dashboard.dashboard_row_pay_title')}
                      </Text>
                      <Text variant="small" textAlign="left">
                        {t('dashboard.dashboard_row_pay_desc', {
                          value: account?.label
                        })}
                      </Text>
                    </div>
                    <Icon style={{ marginLeft: '8px' }} icon="Arrow-small-right" size={12} className="svg-light-grey-1" />

                  </HStack>
                </Box>

              </VStack>
            ) : null }
        </Flex>
      </Box>

      <Box>
        <ContactList showSearch={false} showAdd={false} showEmptyState={false} showCompactView pagination={false} contactsPerView={10} showBack={false} />
      </Box>

      <Box mt="24px" mb="24px">
        <TransactionsList
          accountType={accountType}
          filterRequest={undefined}
          showFilter={false}
          showExport={false}
          showMonth={false}
          showSearch={false}
          showCompactView
          transactionsPerView={20}
          dateFormat="MMM d, yyyy 'at' h:mm aa"
        />
      </Box>

      <Box>
        <CardList ref={cardRef} showSearch={false} showAddCard cardsPerView={20} pagination={false} showExtendedDetails={false} showCompactView />
      </Box>

    </div>

  );
};

export default Dashboard;
